<template>
  <body>

    <div class="desktop-prev-wrapper">
        <button class='desktop-prev' @click="goTomorrow"  add="-1">&#60;</button>
    </div>

    <div class='todoUnit yesterday'>
      
        <TodoUnitHeader 
            :whichDay="'tomorrow'"
            :dateInfoObj="yesterdayObj"
            :changeDate="goTomorrow"
        />

        <body>

          <div class="todoCount">할 일 ({{todoArrYesterday.length}})</div>
          <AddButton 
               :toggleWrite="toggleWrite" 
                thisDay="yesterday"
                :whichDay="whichDay"
                :isWritable="isWritable" 
                :checkEnterThenAddTodo="checkEnterThenAddTodo"
                :todoInputRef="todoInputYesterdayRefWrapper"
                :checkInputStr="checkInputStr"
                :offWrite="offWrite"
                :addTodo="addTodo"
          />
          
          <div class="todo" v-show="todoArrYesterday.length > 0">
              <draggable 
                :list="todoArrYesterday" 
                @end="onDragEnd"
                item-key="id">
                    <template #item="{element,index}">
                              <input 
                                  :type="(state_modifiable && editableIdx == index && whichDay === 'yesterday') ? 'text' : 'button'"
                                  @input="checkInputStr"
                                  minlength="1"
                                  @keyup="checkEnterThenModify" 
                                  @click="toogleEditable"                                   
                                  @dragstart="onDragStart"
                                  @touchstart="onTouchStart"
                                  @touchmove="onTouchMove"
                                  @touchend="onTouchEnd"
                                  @dragover="onDragover"
                                  @drag="onDrag"
                                  @drop="onDrop"                            
                                  :value="element" 
                                  v-bind:key="element.id" 
                                  :index="index"
                                  class='todoEntry'
                                  :class="{editable : (state_editable && !state_modifiable)   && editableIdx == index   &&  whichDay === 'yesterday',
                                  modifiable : state_modifiable === true  && editableIdx == index   &&  whichDay === 'yesterday'                                  }"
                                  whichDay="yesterday"
                                  :asdf="true ? 'selectable' : ''"
                                  :ref=" whichDay === 'yesterday' && editableIdx == index ? 'selectable' : ''"
                                  onfocus="this.type === 'text' ? this.setSelectionRange(this.value.length, this.value.length) : null" 
                              />

                    </template>
              </draggable>    
                <EditMenu      
                  :state_editable="state_editable"
                  :state_modifiable="state_modifiable"
                  :editableIdx="editableIdx"
                  :whichDay="whichDay"
                  :deleteTodo="deleteTodo"
                  :modifyTodo="modifyTodo"
                  :addToDone="addToDone"
                  :cancelModifier="cancelModifier"
                  :completeModifier="completeModifier"
                  :index="index"
                   thisDay="yesterday"
                />
          </div>
            <Done 
                :doneArr="doneArrYesterday" 
                :thisDay="yesterday"
            />  
      </body>

    </div>

    <div class='todoUnit today'>

        <TodoUnitHeader 
            :whichDay="'today'"
            :dateInfoObj="todayObj"
            :changeDate="goTomorrow"
        />

        <body>

            <div class="todoCount">할 일 ({{todoArr.length}})</div>

            <AddButton 
               :toggleWrite="toggleWrite" 
                thisDay="today"
                :whichDay="whichDay"
                :isWritable="isWritable" 
                :checkEnterThenAddTodo="checkEnterThenAddTodo"
                :todoInputRef="todoInputRefWrapper"
                :checkInputStr="checkInputStr"
                :offWrite="offWrite"
                :addTodo="addTodo"
            />
            
            <div class="todo" v-show="todoArr.length > 0">
                <draggable 
                :list="todoArr" 
                item-key="id"
                @start="drag = true"                
                @end="onDragEnd"
                whichDay="today"
                :option="sortableObj"
                >
                    <template #item="{element,index}" whichDay="today" >
                  
                        <input 
                            :type="(state_modifiable && editableIdx == index && whichDay === 'today') ? 'text' : 'button'"
                            @keyup="checkEnterThenModify" 
                            @input="checkInputStr"
                            v-bind="dragOptions"
                            :empty-insert-threshhold="100"
                            ghost-class="ghost"                            
                            @click="toogleEditable" 
                            @dragstart="onDragStart"
                            @touchstart="onTouchStart"
                            @touchmove="onTouchMove"
                            @touchend="onTouchEnd"
                            @dragover="onDragover"
                            @drag="onDrag"
                            @drop="onDrop"
                            :value="element" 
                            v-bind:key="index" 
                            :index="index"
                            class='todoEntry'
                            :class="{editable : (state_editable && !state_modifiable)   && editableIdx == index   &&  whichDay === 'today',
                            modifiable : state_modifiable === true  && editableIdx == index   &&  whichDay === 'today'                                  }"
                            whichDay="today"
                            :asdf="true ? 'selectable' : ''"
                            :ref=" whichDay === 'today' && editableIdx == index ? 'selectable' : ''"
                            onfocus="this.type === 'text' ? this.setSelectionRange(this.value.length, this.value.length) : null" 
                        />
                  
                    </template>
              </draggable>
              
                <EditMenu      
                  :state_editable="state_editable"
                  :state_modifiable="state_modifiable"
                  :editableIdx="editableIdx"
                  :whichDay="whichDay"
                  :deleteTodo="deleteTodo"
                  :modifyTodo="modifyTodo"
                  :addToDone="addToDone"
                  :cancelModifier="cancelModifier"
                  :completeModifier="completeModifier"
                  :index="index"
                   thisDay="today"
                />
            </div>
            <Done 
                :doneArr="doneArr" 
                :thisDay="today"
            />          
        </body>
    </div>

    <div class='todoUnit tomorrow'>

        <TodoUnitHeader 
            :whichDay="'tomorrow'"
            :dateInfoObj="tomorrowObj"
            :changeDate="goTomorrow"
        />        
      <body>
          <div class="todoCount">할 일 ({{todoArrTomorrow.length}})</div>
          <AddButton 
                thisDay="tomorrow"
               :todoInputRef="todoInputTomorrowRefWrapper"
               :toggleWrite="toggleWrite" 
                :whichDay="whichDay"
                :isWritable="isWritable" 
                :checkEnterThenAddTodo="checkEnterThenAddTodo"
                :checkInputStr="checkInputStr"
                :offWrite="offWrite"
                :addTodo="addTodo"
          />
          
          <div class="todo" v-show="todoArrTomorrow.length > 0">
              <draggable 
                :list="todoArrTomorrow" 
                item-key="id"
                @end="onDragEnd"
              >
                    <template #item="{element,index}">
                        <input 
                            :type="(state_modifiable && editableIdx == index && whichDay === 'tomorrow') ? 'text' : 'button'"
                            @input="checkInputStr"
                            minlength="1"
                            @click="toogleEditable" 
                            @keyup="checkEnterThenModify" 
                            @dragstart="onDragStart"
                            @touchstart="onTouchStart"
                            @touchmove="onTouchMove"
                            @touchend="onTouchEnd"
                            @dragover="onDragover"
                            @drag="onDrag"
                            @drop="onDrop"                            
                            :value="element" 
                            v-bind:key="element.id" 
                            :index="index"
                            class='todoEntry'
                            :class="{editable : (state_editable && !state_modifiable)   && editableIdx == index   &&  whichDay === 'tomorrow',
                            modifiable : state_modifiable === true  && editableIdx == index   &&  whichDay === 'tomorrow'                                  }"
                            whichDay="tomorrow"
                            :asdf="true ? 'selectable' : ''"
                            :ref=" whichDay === 'tomorrow' && editableIdx == index ? 'selectable' : ''"
                            onfocus="this.type === 'text' ? this.setSelectionRange(this.value.length, this.value.length) : null" 
                        />

                    </template>
              </draggable>                 
                <EditMenu      
                  :state_editable="state_editable"
                  :state_modifiable="state_modifiable"
                  :editableIdx="editableIdx"
                  :whichDay="whichDay"
                  :deleteTodo="deleteTodo"
                  :modifyTodo="modifyTodo"
                  :addToDone="addToDone"
                  :cancelModifier="cancelModifier"
                  :completeModifier="completeModifier"
                  :index="index"
                   thisDay="tomorrow"
                />
          </div>
            <Done 
                :doneArr="doneArrTomorrow" 
                :thisDay="tomorrow"
            /> 
      </body>
    </div>

    <div class="desktop-prev-wrapper">
        <button class='desktop-next' @click="goTomorrow" add="1">&#62;</button>
    </div>
  </body> 
</template>

<script>
import { ref } from "vue";
import draggable from 'vuedraggable'
import TodoUnitHeader from './TodoUnitHeader.vue'
import EditMenu from './EditMenu.vue'
import AddButton from './AddButton.vue'
import Done from './Done.vue'

const lg = console.log // for fast debug
const l = console.log // for fast debug

const dayObj = {
  1 : '월',
  2 : '화',
  3 : '수',
  4 : '목',
  5 : '금',
  6 : '토',
  0 : '일',
}

function getAnotherDay (todayObj, type) { 

    switch (type) {
      case "어제":
          const yesterday = new Date(todayObj.getTime())
          yesterday.setDate(yesterday.getDate() -1)
          return yesterday
      case "내일":
          const tomorrow = new Date(todayObj.getTime())
          tomorrow.setDate(tomorrow.getDate() +1)
          return tomorrow        
    }
}

function getDateString(dateObj) {
  return `${dateObj.year}.${dateObj.month + 1}.${dateObj.date}`
} 


function getInfoFromLocalhost({todayObj, tomorrowObj, yesterdayObj}){

    // localStorage에서 "할 일"과 "끝난 일"의 정보를 가져온다
    if(typeof localStorage['todo'] === 'undefined') 
      localStorage['todo'] = '{}'
    
    if(typeof localStorage['done']=== 'undefined')
      localStorage['done'] = '{}'
    
    const todoInfo = localStorage['todo'] ?  JSON.parse(localStorage['todo']) : []
    const doneInfo = localStorage['done'] ?  JSON.parse(localStorage['done']) : []

    const todayString = getDateString(todayObj) 
    const todoArr =  todoInfo[todayString] ?  todoInfo[todayString] : []
    const doneArr =  doneInfo[todayString] ?  doneInfo[todayString] : []

    const yesterdayString =   getDateString(yesterdayObj) 
    const todoArrYesterday =  todoInfo[yesterdayString] ?  todoInfo[yesterdayString] : []
    const doneArrYesterday =  doneInfo[yesterdayString] ?  doneInfo[yesterdayString] : []
    
    const tomorrowString = getDateString(tomorrowObj) 
    const todoArrTomorrow =  todoInfo[tomorrowString] ?  todoInfo[tomorrowString] : []
    const doneArrTomorrow =  doneInfo[tomorrowString] ?  doneInfo[tomorrowString] : []
    return {todoArr, doneArr, todoArrYesterday, doneArrYesterday, todoArrTomorrow, doneArrTomorrow}
}


export default {

  components: { 
    Done,
    AddButton,
    EditMenu,
    TodoUnitHeader,
    draggable 
  },
  setup() {

    let latestTouchTime ; 
    let relativeXPos
    let relativeYPos
    let lastOffsetX  ; 

    const offWrite = _ =>  isWritable.value = false 

      const sortableObj = {}

      const dragOptions = {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost"
      }

      let isOnTouchMoveRuns = false 
      
      let dragStartPosition = {
        x: null,
        y:null,
        width: null,
        height : null,
        xPosForDelete : null,
        xPosForDone : null,
        clientX : null
      }

    // vue에서 사용할 스테이트를 리턴한다
    function getRef(){

      const dateObj = new Date()
      const todayObj = {
              date : dateObj.getDate(),
              year : dateObj.getFullYear(),
              month : dateObj.getMonth(),
              day : dayObj[dateObj.getDay()] ,            
      }
      const yesterday = getAnotherDay (dateObj, '어제')

      const yesterdayObj = {
              date : yesterday.getDate(),
              year : yesterday.getFullYear(),
              month : yesterday.getMonth(),
              day : dayObj[yesterday.getDay()] ,            
      }

      const tomorrow = getAnotherDay (dateObj, '내일')
      const tomorrowObj = {
              date : tomorrow.getDate(),
              year : tomorrow.getFullYear(),
              month : tomorrow.getMonth(),
              day : dayObj[tomorrow.getDay()] ,            
      }


      const {todoArr, doneArr, 
      todoArrYesterday, doneArrYesterday, 
      todoArrTomorrow, doneArrTomorrow} =  getInfoFromLocalhost({todayObj, tomorrowObj, yesterdayObj})

      return {
        currentMovingRef: ref(false),
        state_editable : ref(false),
        state_modifiable : ref(false),
        doneArr : ref(doneArr),
        doneArrYesterday : ref(doneArrYesterday),
        doneArrTomorrow : ref(doneArrTomorrow),
        todoArrTomorrow : ref(todoArrTomorrow ),
        todoArrYesterday : ref(todoArrYesterday ),
        editableIdx : ref(null ),
        isEdit : ref(false ),
        isWritable : ref(false),
        todoArr : ref(todoArr),
        todoInputRef : ref(null),
        todoInputYesterdayRef : ref(null),
        todoInputTomorrowRef : ref(null),
        todayObj : ref(todayObj),
        tomorrowObj : ref(tomorrowObj),
        yesterdayObj : ref(yesterdayObj),
        date : ref(dateObj.getDate()),
        year : ref(dateObj.getFullYear()),
        month : ref(dateObj.getMonth()),
        day : ref(dateObj.getDay()),
        selectable : ref(null),
        whichDay : ref(null),
        state_drag : ref(false),
      }
    }

    const {currentMovingRef, state_drag, state_editable, state_modifiable, 
    doneArr, doneArrYesterday, doneArrTomorrow, todoInputYesterdayRef, 
    todoInputTomorrowRef, todoArrTomorrow, todoArrYesterday, whichDay, 
    selectable, editableIdx, isEdit, isWritable, todoArr, todoInputRef, 
    todayObj, tomorrowObj, yesterdayObj, date, year, month, day} = getRef()

    function setWritableState(){

        isWritable.value = (!isWritable.value)
        state_editable.value = false
        isEdit.value = false
        state_modifiable.value = false
    }

    const toggleWrite = e => {

        setWritableState()

        whichDay.value = e.target.attributes.whichDay.value

        const dayToInputRefMapper = {
          'yesterday' : todoInputYesterdayRef,
          'today' : todoInputRef,
          'tomorrow' : todoInputTomorrowRef,
        }

        const currentInputRef = dayToInputRefMapper[whichDay.value]

        initUserInput(currentInputRef)
    }

    function initUserInput(currentInputRef){
        currentInputRef.value.value = ''

        setTimeout(() => currentInputRef.value.focus(), 
                  0);
    }

    function getDateString(dateObj) {
      return `${dateObj.year}.${dateObj.month + 1}.${dateObj.date}`
    } 

    const addTodo = () => {      

      const dayInfo = {
        'yesterday' : {
          dateObj : yesterdayObj,
          inputRef : todoInputYesterdayRef,
          currentTodoArrRef : todoArrYesterday,
        },
        'today' : {
          dateObj : todayObj,
          inputRef : todoInputRef,
          currentTodoArrRef : todoArr,
        },
        'tomorrow' : {
          dateObj : tomorrowObj,
          inputRef : todoInputTomorrowRef,
          currentTodoArrRef : todoArrTomorrow,
        },
      }

      const {dateObj, inputRef, currentTodoArrRef} = dayInfo[whichDay.value]

      const dateString = getDateString(dateObj.value)      
      const todoInput = inputRef.value.value
      const currentTodoInputRef =  inputRef.value
      const currentTodoArr =  currentTodoArrRef.value

      if(todoInput.length > 0) 
        currentTodoArrRef.value.push(todoInput)      

      saveTodoArrToLocalStorage({currentTodoArr, dateString})

      clearUserInput(currentTodoInputRef)
    }

    function clearUserInput(currentTodoInputRef){

      currentTodoInputRef.value = ''

      setTimeout(() => {
            currentTodoInputRef.focus()
      },0 )
    }

    function saveTodoArrToLocalStorage({currentTodoArr, dateString}){

      const allTodoInfo = JSON.parse(localStorage['todo'])      
      allTodoInfo[dateString] = currentTodoArr  
      localStorage.setItem('todo', JSON.stringify(allTodoInfo))
    }

    function setEditableState() {
          isWritable.value = false 
          state_editable.value = true 
          isEdit.value = ! isEdit.value
          state_modifiable.value = false
    }

    function setYPosToCenter(e){

        const elementY = e.target.getBoundingClientRect().top
        const currentScrollY = window.scrollY
        const targetY = (elementY +  currentScrollY) - (window.innerHeight / 2)
        window.scroll(0, targetY)
    }

    const toogleEditable = e => {

      setYPosToCenter(e)

      setEditableState()

      editableIdx.value = e.target.attributes.index.value
      whichDay.value = e.target.attributes.whichDay.value
    } 

    // 좌우 화살표를 눌렀을 때 날짜를 이동한다
    // 함수명 리팩토링 필요
    const goTomorrow = e => {

      const addDay = e.target.attributes.add.value | 0 

      const newToday =  new Date(todayObj.value.year, todayObj.value.month, todayObj.value.date )
      newToday.setDate(newToday.getDate() + addDay)
      todayObj.value = {
          date : newToday.getDate(),
          year : newToday.getFullYear(),
          month : newToday.getMonth() ,
          day : dayObj[newToday.getDay()] ,            
      }

      const newYesterday = getAnotherDay (newToday, '어제')
      yesterdayObj.value = {
          date : newYesterday.getDate(),
          year : newYesterday.getFullYear(),
          month : newYesterday.getMonth() ,
          day : dayObj[newYesterday.getDay()] ,            
      }

      const newTomorroww = getAnotherDay (newToday, '내일')
      tomorrowObj.value = {
          date : newTomorroww.getDate(),
          year : newTomorroww.getFullYear(),
          month : newTomorroww.getMonth() ,
          day : dayObj[newTomorroww.getDay()] ,            
      }

      // 로컬스토리지에서 "할 일"과 "끝난 일"을 가져온다
      const todoInfo = localStorage['todo'] ?  JSON.parse(localStorage['todo']) : []
      const doneInfo = localStorage['done'] ?  JSON.parse(localStorage['done']) : []

      const dateString = getDateString(todayObj.value) 
      todoArr.value =  todoInfo[dateString] ?  todoInfo[dateString] : []
      doneArr.value =  doneInfo[dateString] ?  doneInfo[dateString] : []

      const dateStringYesterday = getDateString(yesterdayObj.value) 
      todoArrYesterday.value =  todoInfo[dateStringYesterday] ?  todoInfo[dateStringYesterday] : []
      doneArrYesterday.value =  doneInfo[dateStringYesterday] ?  doneInfo[dateStringYesterday] : []

      const dateStringTomorrow = getDateString(tomorrowObj.value) 
      todoArrTomorrow.value =  todoInfo[dateStringTomorrow] ?  todoInfo[dateStringTomorrow] : []
      doneArrTomorrow.value =  doneInfo[dateStringTomorrow] ?  doneInfo[dateStringTomorrow] : []

      window.scroll(0, 0)
    }

    const modifyTodo = e => {

      setTimeout(() => {
        selectable.value.focus()
        selectable.value.scrollLeft = selectable.value.scrollWidth;    
        selectable.value.setSelectionRange(selectable.value.value.length, selectable.value.value.length) 
      }, 0);

      state_editable.value = false 
      state_modifiable.value = true
      
      setYPosToCenter(e) 
    }
    
    const deleteTodo = () => {

      const whichDayToTodoArrMapper = {
        'yesterday' : todoArrYesterday,
        'today' : todoArr,
        'tomorrow' : todoArrTomorrow,
      }

      let targetTodoArr = whichDayToTodoArrMapper[whichDay.value]
      let dateString 

      switch (whichDay.value) {    

        case 'yesterday':    
              dateString = getDateString(yesterdayObj.value) 
          break;

        case 'today':    
              dateString = getDateString(todayObj.value) 
          break;

        case 'tomorrow':    
              dateString = getDateString(yesterdayObj.value) 
          break;
      }

      // todo 배열을 갱신한다
      const todoArrClone = JSON.parse(JSON.stringify(targetTodoArr.value))      
      todoArrClone.splice(editableIdx.value, 1)
      targetTodoArr.value = todoArrClone  

      saveTodoToLocalstorage({targetTodoArr, dateString})

      editableIdx.value = null
    }

    function saveTodoToLocalstorage({targetTodoArr, dateString}) {
      const allTodoInfo = JSON.parse(localStorage['todo'])
      allTodoInfo[dateString] = targetTodoArr.value   
      localStorage.setItem('todo', JSON.stringify(allTodoInfo))
    }
    
    const addToDone = () => {

      const whichDayToTodoArrMapper = {
        'yesterday' : todoArrYesterday,
        'today' : todoArr,
        'tomorrow' : todoArrTomorrow,
      }

      let targetTodoArr = whichDayToTodoArrMapper[whichDay.value]

      const whichDayToDateInfoMapper = {
        'yesterday' : yesterdayObj,
        'today' : todayObj,
        'tomorrow' : tomorrowObj,
      }
          
      const dateString = getDateString(whichDayToDateInfoMapper[whichDay.value].value)      

/* 
      let dateString 

      switch (whichDay.value) {    

        case 'yesterday':    
              dateString = getDateString(yesterdayObj.value) 
          break;

        case 'today':    
              dateString = getDateString(todayObj.value) 
          break;

        case 'tomorrow':    
              dateString = getDateString(tomorrowObj.value) 
          break;
      }
 */
      const todoArrClone = JSON.parse(JSON.stringify(targetTodoArr.value))      
      const targetTodoEntryArr = todoArrClone.splice(editableIdx.value, 1)
      targetTodoArr.value = todoArrClone

      editableIdx.value = null

      const allTodoInfo = JSON.parse(localStorage['todo'])
      allTodoInfo[dateString] = targetTodoArr.value   
      localStorage.setItem('todo', JSON.stringify(allTodoInfo))

      const whichDayToDoneArrMapper = {
        'yesterday' : doneArrYesterday,
        'today' : doneArr,
        'tomorrow' : doneArrTomorrow,
      }

      let targetDoneArr = whichDayToDoneArrMapper[whichDay.value]

      const targetDoneArrModified = [...targetTodoEntryArr]
      const targetDoneArrFinal = targetDoneArrModified.concat(targetDoneArr.value)

      targetDoneArr.value = targetDoneArrFinal

      localStorage['done'] = localStorage['done'] === `` ? `{}` : localStorage['done'] 

      const doneDB = JSON.parse(localStorage['done'])
      doneDB[dateString] = targetDoneArr.value
      localStorage.setItem('done', JSON.stringify(doneDB))
    }

      const checkInputStr = e =>{

        const userInput = e.target.value
        const INPUT_MAX_LENGTH = 20
        if(userInput.length >= INPUT_MAX_LENGTH) {
          const userInputFiltered = userInput.substring(0,20)
          e.target.value = userInputFiltered
          e.target.classList.add('inputTooLong')
        } else {
          e.target.classList.remove('inputTooLong')
        }
      }
      
      const cancelModifier = e => {
        state_editable.value = true 
          state_modifiable.value = false
      }

      const removeCloneElement = _ => {

          setTimeout(() => {
              if(document.querySelector('.clone'))  {
                document.querySelector('.clone').remove()
              }            
          }, 0);
      }

      // Predidate 
      function checkDragPositionToDone(lastOffsetX, dragStartPosition) {
        return lastOffsetX > dragStartPosition.clientX + (dragStartPosition.width / 2)
      }

      
      const onDragEnd = e=> {

          state_drag.value = false

          if(! navigator.userAgentData.mobile)
            currentMovingRef.value.style.opacity = 1          

          const opacity = 1 -  (dragStartPosition.x - lastOffsetX) / (dragStartPosition.width / 2)
 
          let flag_delete_or_done = false ; 

          if(opacity < 0) {
            flag_delete_or_done = true 
            deleteTodo()
          }
          // if(lastOffsetX >  dragStartPosition.clientX + (dragStartPosition.width / 2)){
          if(checkDragPositionToDone(lastOffsetX, dragStartPosition)){
            flag_delete_or_done = true 
            addToDone()
          }          

          removeCloneElement()
        // 이것을 추가하지 않으면 삭제나 완료작업이 수행되지 않음
          if(!flag_delete_or_done)          
            saveTodoArrToLocalstorage(e)
      }

      function saveTodoArrToLocalstorage(e){

            const currentDay = whichDay.value 

            const childrenArr = Array(...e.target.querySelectorAll('input')) 

            const newTodoArr = childrenArr.map(childElem => childElem.value)

            const whichDayToTodoArrMapper = {
              'yesterday' : todoArrYesterday,
              'today' : todoArr,
              'tomorrow' : todoArrTomorrow,
            }

            const whichDayToDateInfoMapper = {
              'yesterday' : yesterdayObj,
              'today' : todayObj,
              'tomorrow' : tomorrowObj,
            }
          
            const targetTodoRef = whichDayToTodoArrMapper[currentDay]        
            targetTodoRef.value = newTodoArr         
            const dateString = getDateString(whichDayToDateInfoMapper[currentDay].value)      
            const allTodoInfo = JSON.parse(localStorage['todo'])
            allTodoInfo[dateString] = targetTodoRef.value   
            localStorage.setItem('todo', JSON.stringify(allTodoInfo))               
      }


    const completeModifier = () => {

      let dateString ;
      let todoInput ;
      let currentTodoArrClone

      const whichDayToTodoArrMapper = {
        'yesterday' : todoArrYesterday,
        'today' : todoArr,
        'tomorrow' : todoArrTomorrow,
      }

      const whichDayToDateInfoMapper = {
        'yesterday' : yesterdayObj,
        'today' : todayObj,
        'tomorrow' : tomorrowObj,
      }

      const currentTodoArrRef = whichDayToTodoArrMapper[whichDay.value]
      const currentTodoArr = currentTodoArrRef.value       

      dateString = getDateString(whichDayToDateInfoMapper[whichDay.value].value)   

      todoInput = selectable.value.value

      currentTodoArrClone =  JSON.parse(JSON.stringify(currentTodoArr))
    
      if(todoInput.length > 0) {
            currentTodoArrClone[editableIdx.value] = todoInput
            currentTodoArrRef.value = currentTodoArrClone
      }

      const allTodoInfo = JSON.parse(localStorage['todo'])
      allTodoInfo[dateString] = currentTodoArr
  
      localStorage.setItem('todo', JSON.stringify(allTodoInfo))

      selectable.value.attributes.type.value = 'button'

      initState()

      saveTodoArrToLocalStorage({currentTodoArr : currentTodoArrRef.value, dateString})
    }

      function initState(){
        
        isWritable.value = false      
        state_editable.value = false
        state_modifiable.value = false
        editableIdx.value = null
        selectable.value = null
      }

      const checkEnterThenAddTodo = e => {      

            if (e.key === 'Esc' || e.key === 'Escape'){
              isWritable.value = false                    
            }

            if (e.key === 'Enter' || e.keyCode === 13) 
                addTodo()            
      }

      function checkDoubleClick({latestTouchTime, currentTouchTime}){
        const MAXIMUM_DOUBLE_CLICK_THRESHHOLD_MS = 400
        return latestTouchTime != null && 
                currentTouchTime - latestTouchTime < MAXIMUM_DOUBLE_CLICK_THRESHHOLD_MS
      }

      const onTouchStart = e => {

        // setYPosToCenter(e)

        const touchTime = new Date().getTime()
        const currentTouchTime =  touchTime 
/* 
        if(checkDoubleClick({latestTouchTime, currentTouchTime})) {
          addToDone() 
        }
         */
        latestTouchTime =  touchTime

        isOnTouchMoveRuns = false 

        currentMovingRef.value =  e.target
        whichDay.value = e.target.attributes.whichDay.value
        editableIdx.value =  Number(e.target.attributes.index.value) 

        setMobileDragStartPosition(e)

      }

      function setMobileDragStartPosition(e){

        const x =  e.touches[0].clientX;

        dragStartPosition.clientX = e.touches[0].clientX
        dragStartPosition.x = x
        dragStartPosition.xPosForDelete = x - (e.target.offsetWidth / 2)
        dragStartPosition.xPosForDone = x + (e.target.offsetWidth / 2)
        dragStartPosition.width = e.target.offsetWidth
      }

      function insertClonedBtnToHTML(targetElement) {
          const cloned = targetElement.cloneNode()
          cloned.style.position = 'absolute'
          cloned.style['z-index'] = -9999
          cloned.style.display = 'none'
          cloned.classList.add('clone')        
          document.querySelector('.portal').innerHTML +=  cloned.outerHTML
      }

      const onDragStart = e => {
        
        currentMovingRef.value = e.target
        const targetElement = e.target 

        targetElement.style.opacity = 0     
        
        insertClonedBtnToHTML(targetElement) 
        
        var rect = e.target.getBoundingClientRect();

        // 함수 외부에서 공유할 공유변수를 선언한다
        relativeXPos = e.clientX - rect.left
        relativeYPos = e.clientY - rect.top       

        // ref를 수정한다
        whichDay.value = targetElement.attributes.whichDay.value
        editableIdx.value = Number(targetElement.attributes.index.value) 

        setDragStartPositionProps({e, targetElement})
      }

      function setDragStartPositionProps({e, targetElement}){
        
            const xPosForDelete = e.offsetX - (targetElement.offsetWidth / 2)
            const xPosForDone  =  e.offsetX + (targetElement.offsetWidth / 2)

            // dragStartPosition 객체 
            dragStartPosition.x = e.clientX 
            dragStartPosition.y = e.clientY
            dragStartPosition.width = targetElement.offsetWidth
            dragStartPosition.height = targetElement.offsetHeight
            dragStartPosition.clientX  = e.clientX 
            dragStartPosition.xPosForDelete = xPosForDelete
            dragStartPosition.xPosForDone = xPosForDone
      }

      function overlapClonedBtn(e) {
        const clonedNode = document.querySelector('.clone')
        clonedNode.style.display = 'block'        
        clonedNode.style.left = (e.clientX - relativeXPos + window.scrollX).toString() + 'px';
        clonedNode.style.top = (e.clientY - relativeYPos + window.scrollY).toString() + 'px';
        return clonedNode
      }

      const onDrag = e => {
       
        const clonedNode = overlapClonedBtn(e)
        
        const currX = e.clientX

        lastOffsetX = (typeof e.clientX === 'number' && e.clientX !== 0 ) ?  e.clientX :  lastOffsetX

        // dragStartPosition.clientY
        if(e.clientX < dragStartPosition.clientX) {
            const opacity = 1 -  (dragStartPosition.x - e.clientX) / (clonedNode.clientWidth / 2)
            clonedNode.style.opacity =  opacity
        }

        if(currX > dragStartPosition.x) {
          changeGhostBtnBackgroundColor({dragStartPosition, currX, ghostBtn : clonedNode})
        }
      }
      
      const onDrop =  e => {        
          const [diffX,  diffY] = [dragStartPosition.x - e.offsetX, dragStartPosition.y - e.offsetY]                    
      }

      const onDragover = e => {
        return false 
      }

      const onTouchMove = e => {

        isOnTouchMoveRuns = true

        const currX =          e.touches[0].clientX 
        const currentClientX = e.touches[0].clientX 

        if(checkLeftMove({currentClientX,dragStartPosition})) {          
        // if(currentClientX < dragStartPosition.clientX) {          
          changeGhostBtnOpacity({dragStartPosition, currentClientX})
        }

        if(checkRightMove({currentClientX, dragStartPosition})) {
          const ghostBtn = document.querySelector('.sortable-fallback')
          changeGhostBtnBackgroundColor({dragStartPosition, currX, ghostBtn})
        }      
      }

      function checkRightMove({currentClientX, dragStartPosition}){
          return currentClientX > dragStartPosition.x
      }

      function checkLeftMove({currentClientX,dragStartPosition}){
        return currentClientX < dragStartPosition.clientX
      }

      function changeGhostBtnOpacity({dragStartPosition, currentClientX}){
         
          const opacity = 1 -  (dragStartPosition.clientX - currentClientX) / (dragStartPosition.width / 2)
          document.querySelector('.sortable-fallback').style.opacity = opacity 
      }

      function changeGhostBtnBackgroundColor({dragStartPosition, currX, ghostBtn}){
        
          const startRGB = {
            r:parseInt(`00`, 16),
            g:parseInt(`7A`, 16),
            b:parseInt(`FF`, 16),
          }

          const endRGB = {
            r:parseInt(`96`, 16),
            g:parseInt(`9D`, 16),
            b:parseInt(`AB`, 16),
          }

          const diffRGB = {
            r: endRGB.r - startRGB.r,
            g: endRGB.g - startRGB.g,
            b:  endRGB.b - startRGB.b,
          }

          const startX = dragStartPosition.x; 
          const endX =  dragStartPosition.x + (dragStartPosition.width / 2)
          const xline = endX - startX

          const currentXPosPercentile = (currX - startX) / xline
          
          const newR = startRGB.r +  (diffRGB.r * currentXPosPercentile)
          const newG = startRGB.g +  (diffRGB.g * currentXPosPercentile)
          const newB = startRGB.b +  (diffRGB.b * currentXPosPercentile)

          const newRHex = newR.toString(16).substring(0,2);
          const newGHex = newG.toString(16).substring(0,2);
          const newBHex = newB.toString(16).substring(0,2);          

          const MAXIMUM_RED_COLOR_THRESHOLD = 150
          const DESTINATION_COLOR = `#969DAB`
/* 
          let color = '#' + newRHex + newGHex  + newBHex
          if(newR >= MAXIMUM_RED_COLOR_THRESHOLD) {
                color = DESTINATION_COLOR
          }
 */
          const newColor = (newR >= MAXIMUM_RED_COLOR_THRESHOLD) ? 
                                DESTINATION_COLOR : 
                                '#' + newRHex + newGHex  + newBHex

          ghostBtn.style.background = newColor
          ghostBtn.style.opacity = 1
      }

      function setTouchEndState() {
          isWritable.value = false 
          isEdit.value = ! isEdit.value
          state_editable.value = true 
          state_modifiable.value = false
      }

      function checkMobileDeviceAndNotDragging() {
          return navigator.userAgentData.mobile &&
                  isOnTouchMoveRuns === false
      }

      const onTouchEnd = e => {

        if(checkMobileDeviceAndNotDragging() ) {

          isOnTouchMoveRuns = false 

          setYPosToCenter(e)

          setTouchEndState()
          /* 
          isWritable.value = false 
          state_editable.value = true 
          isEdit.value = ! isEdit.value
          state_modifiable.value = false
          */
          editableIdx.value = e.target.attributes.index.value
          whichDay.value = e.target.attributes.whichDay.value

          if(isOnTouchMoveRuns)
            editableIdx.value = null 
        }

        e = e.changedTouches[e.changedTouches.length-1];

        const x = e.clientX

        if(x < dragStartPosition.xPosForDelete) {           
              deleteTodo()
              removeCloneElement()
        }

        if(x > dragStartPosition.xPosForDone){
              addToDone() 
        }
      }

      const todoInputYesterdayRefWrapper = () => todoInputYesterdayRef
      const todoInputTomorrowRefWrapper = () => todoInputTomorrowRef
      const todoInputRefWrapper = () => todoInputRef

      const checkEnterThenModify = e => {

        if (e.key === 'Enter' || e.keyCode === 13) {
          completeModifier() 
        }
      }

      return { 
        todoInputTomorrowRefWrapper,
        todoInputRefWrapper,
        checkEnterThenModify,
        onTouchEnd,
        onTouchMove,
        onTouchStart,
        onDragover,
        onDrop,
        onDragStart,
        onDrag,
        dragOptions,
        state_drag,
        sortableObj,
        checkEnterThenAddTodo,
        onDragEnd,
        cancelModifier,
        completeModifier,
        state_editable,
        state_modifiable,
        checkInputStr,
        doneArr, 
        doneArrYesterday, 
        doneArrTomorrow,
        addToDone,
        todoInputYesterdayRef, 
        todoInputYesterdayRefWrapper, 
        todoInputTomorrowRef,
        todoArrTomorrow, 
        todoArrYesterday,
        whichDay,
        modifyTodo,
        deleteTodo,
        date, 
        year, 
        month, 
        day, 
        isWritable, 
        toggleWrite, 
        offWrite, 
        addTodo,
        todoInputRef,
        todoArr,
        isEdit,
        toogleEditable,
        editableIdx,
        goTomorrow,
        todayObj,
        tomorrowObj,
        yesterdayObj,
        selectable
      };
    }
  };

</script>

